import React from 'react';
import {connect} from 'react-redux';
import {getWard, downloadBox} from 'store/actions/box.actions';
import {staticHost} from 'constants/api.constants';

import HeaderBox from "components/header-box.jsx";
import img from 'assets/img/img.png';

const mapStateToProps = state => ({
    ward: state.box.ward,
    box: state.box.box
});

const mapDispatchToProps = {
    getWard,
    downloadBox
};

class BoxPerson extends React.Component {
    componentDidMount() {
        this.props.getWard(this.props.match.params.boxId);
        this.props.downloadBox(this.props.match.params.boxId);
    }

    render() {
        const ward = this.props.ward.user;
        const box = this.props.box;

        return (
            <div>
                <HeaderBox count={box && box.users.length} time={box && box.dateDistribution}/>
                <div className="container">
                    <div className="wr-box">
                        <p className="wr-box__description wr-box__description_center">
                            Чари почалися! Теперь потрібно обрати подарунки!
                        </p>
                        <h2 className="title title__blue">Твій участник</h2>
                        <div className="profile">
                            <form action="">
                                {ward && <img src={staticHost + ward.photo} alt=""/>}
                                {ward ? claims.map(item => (
                                    <ProfileClaim key={item.title} label={item.title}>{ward[item.key]}</ProfileClaim>
                                )) : <div>Розподіл скоро відбудеться...</div>}
                            </form>
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}

const claims = [
    {title: 'Ім\'я', key: 'firstName'},
    {title: 'Прізвище', key: 'lastName'},
    {title: 'Стать', key: 'gender'},
    {title: 'Дата народження', key: 'yearOfBirth'},
    {title: 'Номер телефону', key: 'phone'},
    {title: 'Країна', key: 'country'},
    {title: 'Місто', key: 'city'},
    {title: 'Адреса', key: 'address'},
    {title: 'Поштовий індекс', key: 'postcode'},
];

const ProfileClaim = ({label, children}) => (
    <div className="profile_input">
        <span>{label}</span>
        <div>{children}</div>
    </div>
);

export default connect(mapStateToProps, mapDispatchToProps)(BoxPerson);
